.form__input {
    margin: 0;
    padding: 20px;
    max-width: 600px;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
    resize: none;
    text-align: start;
    word-wrap: break-word;
    font-family: Spline Sans;
}

@media screen and (max-width: 800px) {
    .form__input {
        padding: 15px;
    }
}